import { Component, OnInit, AfterContentInit, AfterContentChecked, AfterViewChecked, AfterViewInit, OnDestroy, OnChanges, DoCheck, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-lifecycle',
  templateUrl: './lifecycle.component.html',
  styleUrls: ['./lifecycle.component.css']
})
export class LifecycleComponent implements OnInit, AfterContentInit, AfterContentChecked, AfterViewInit, AfterViewChecked, OnDestroy, OnChanges, DoCheck {
  test = 'hi';
  constructor() {
    console.log('constructor')
  }
  ngOnChanges(changes: SimpleChanges) {
    console.log(changes);
    console.log('ngOnChanges');
  }
  ngOnInit() {
    console.log('ngOnInit')
  }
  ngDoCheck() {
    console.log('ngDoCheck');
  }
  ngAfterContentInit() {
    console.log('ngAfterContentInit');
  }
  ngAfterContentChecked() {
    console.log('ngAfterContentChecked');
  }
  ngAfterViewInit() {
    console.log('ngAfterViewInit');
  }
  ngAfterViewChecked() {
    console.log('ngAfterViewChecked');
  }
  ngOnDestroy() {
    console.log('ngOnDestroy')
  }

}
