import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class ApiService {
  origin: string = 'https://jsonplaceholder.typicode.com/';
  constructor(private http: HttpClient) { }



  post(subpath: string, data: any): Observable<any> {
    let url = this.origin + subpath;
    return this.http.post(url, data);
  }
  put(subpath: string, data: any): Observable<any> {
    let url = this.origin + subpath;
    return this.http.put(url, data);
  }
  delete(subpath: string, data: any): Observable<any> {
    let url = this.origin + subpath;
    return this.http.delete(url);
   
  }

}