import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { SigninComponent } from './signin/signin.component';
import { ErrorpageComponent } from './errorpage/errorpage.component';
import { DummyComponent } from './dummy/dummy.component';
import { PaginationComponent } from './pagination/pagination.component';
import { HeaderComponent } from './header/header.component';
import { ReactiveFormsModule } from '@angular/forms';
import { EmployeelistComponent } from './employeelist/employeelist.component';
import { SimplePipeComponent } from './simple-pipe/simple-pipe.component';
import { TranslatePipe } from './pipes/translate.pipe';
import { LanguagePipe } from './language.pipe';
import { EmployeedetailComponent } from './employeedetail/employeedetail.component';
import { BarchartComponent } from './barchart/barchart.component';



const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'signin', component: SigninComponent },
  { path: 'errorpage', component: ErrorpageComponent },
  { path: 'dummy', component: DummyComponent },
  { path: 'pagination', component: PaginationComponent },
  { path: 'header', component: HeaderComponent },
  { path: 'ReactiveFormsModule', component: ReactiveFormsModule },
  { path: 'employeelist', component: EmployeelistComponent },
  { path: 'employeedetail', component: EmployeedetailComponent },
  { path: 'simplepipe', component: SimplePipeComponent },
  { path: 'translatepipe', component: TranslatePipe },
  { path: 'Languagepipe', component: LanguagePipe },
  { path: 'barchart', component: BarchartComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
